@charset "utf-8";

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap');

*{margin: 0; padding: 0;}
body{
  font-family: "Noto Sans", sans-serif;
  font-size: 13px;
  color: #333;
  background-color: #eff3f7;
}
a{
  color: #333;
  text-decoration: none;
  font-size: 16px;
}
ul li{
  list-style: none;
}

/* 공통서식 */

/* 상단 헤더 서식 */
.header{
  width: 200px;
  height: 100%;
  text-align: center;
  position: fixed;
  top: 0px; left: 0px;
  z-index: 100;

}
h1{
  padding: 70px 0px 50px 0px;
}
/* 네비게이션서식 */
.navi {
  padding-top: 70px;
  text-align: center;
}
.navi li{
  line-height: 40px;
}
.navi a{
  text-decoration: none;
  font-size: 16px;
  padding: 5px 10px;
  color: #5d5d5d;
  text-shadow: 0px 1px 1px #adadad;
}
.navi a:hover{
  text-decoration: underline;
}

main{
  width: calc(100% - 200px);
  height: 100%;
  position: fixed;
  left: 200px;
  top: 0px;
  padding-top: 50px;
  overflow: scroll;
}
/* 푸터서식 */
.footer{
  width: 100%;
  line-height: 40px;
  background-color: #333;

}
.footer address{
  color: #fff;
  text-align: center;
  font-size: normal;
}

/* 가로 해상도 1025이 경우 서식 */
@media screen and(min-width:1025px) {
  
}
/* 테블릿 서식 :가로 해상도 768 - 1024이 경우 서식 */
@media screen and (min-width: 768px) and (max-width: 1024px) {

  .header {
    width: 180px;
  }

  main {
    width: calc(100% - 180px);
    left: 180px;
    padding-top: 40px;
  }

  h1 {
    padding: 50px 0 40px;
  }

  .navi {
    padding-top: 40px;
  }
}

/* 모바일 서식 */
@media screen and (max-width: 767px) {

  /* ===== 헤더를 상단바로 ===== */
  .header {
    width: 100%;
    height: auto;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    border-bottom: 1px solid #ddd;
    z-index: 1000;
  }

  h1 {
    padding: 15px 0;
  }

  /* ===== 네비 가로 정렬 ===== */
  .navi {
    padding: 0;
  }

  .navi ul {
    display: flex;
    justify-content: center;
    gap: 15px;
    padding-bottom: 10px;
  }

  .navi li {
    line-height: normal;
  }

  .navi a {
    font-size: 14px;
  }

  /* ===== 메인 영역 전체폭 ===== */
  main {
    width: 100%;
    position: relative;
    left: 0;
    top: 0;
    padding: 120px 15px 20px;
    overflow: visible;
  }
}
