.movie_wrap {
  width: 1400px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  margin: 0 auto;
}

.movie_list {
  position: relative;
  background: #fff;
  padding: 15px 0px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
}

.poster_box {
  transform: translate(30px, -30px);
  width: 180px;
  height: 260px;
  float: left;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.5);
  overflow: hidden;
}

.poster_box img {
  width: 180px;
}

.info_box {
  width: 60%;
  float: left;
  padding-left: 50px;
  line-height: 25px;
}

.info_box>p:first-child {
  display: none;
}

.info_box>p:nth-child(2) {
  font-size: 16px;
  font-weight: bold;
}

.info_box>p:last-child {
  color: #f00;
  font-weight: bold;
  position: absolute;
  right: 30px;
  bottom: 30px;
}

.movie_list,
.poster_box {
  transition: transform 0.35s ease, box-shadow 0.35s ease;
}


.movie_list:hover {
  transform: translateY(-8px);
  box-shadow: 0 18px 30px rgba(0, 0, 0, 0.15);
}


.movie_list:hover .poster_box {
  transform: translate(40px, -45px) scale(1.05);
  box-shadow: 0 18px 28px rgba(0, 0, 0, 0.6);
}
@media (max-width: 1024px) {
  .movie_wrap {
    width: 100%;
    padding: 0 20px;
    grid-template-columns: 1fr;
  }

  .poster_box {
    transform: translate(20px, -20px);
    width: 160px;
    height: 230px;
  }

  .poster_box img {
    width: 160px;
  }

  .info_box {
    width: calc(100% - 180px);
    padding-left: 40px;
    line-height: 22px;
  }

  .movie_list:hover .poster_box {
    transform: translate(30px, -30px) scale(1.03);
  }
}
@media (max-width: 768px) {
  .movie_wrap {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .movie_list {
    padding: 20px;
  }

  .poster_box {
    float: none;
    margin: 0 auto 15px;
    transform: none;
    width: 150px;
    height: 220px;
  }

  .poster_box img {
    width: 150px;
  }

  .info_box {
    float: none;
    width: 100%;
    padding-left: 0;
    text-align: center;
  }

  .info_box > p:last-child {
    position: static;
    margin-top: 10px;
  }

  /* 모바일에서는 hover 제거 */
  .movie_list:hover,
  .movie_list:hover .poster_box {
    transform: none;
    box-shadow: none;
  }
}
